.about-container{
    width: 100%;
    display:flex;
    flex-direction: row;
    background-color: white;
    color: black;
    height: 50%;
    padding: 100px 0px 100px 0px;
    
}
.about-descripcion{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width:  50%;
    padding: 0 100px 0 100px;
    color: transparent;
    margin: 0 auto;
    transition: all 1s ease;

}
.about-descripcion > h3{
    margin-bottom:30px;
    font-size: 40px;
    
}
.about-desc >p{
    line-height: 1.5;
}
.about-img{
    width: 50%;
    display: flex;
    margin: 0 auto;
   
}
.about-img-img{
    max-width: 700px;
    width: 70%;
    height: auto;
    margin: 0 auto;
    -webkit-box-shadow: 5px 5px 28px 3px rgba(0,0,0,0.74); 
    box-shadow: 5px 5px 28px 3px rgba(0,0,0,0.74);
    border-radius: 10px;
    opacity: 0;
    transition: all 1s ease;
}
.scrolling-about{
    color:black;
    transition: all 1s ease;
}
 .scrolling-img{
    opacity: 1;
    transition: all 1s ease;
 }

 @media (min-width: 320px) and (max-width: 767px){
    .about-container{
        flex-direction: column;
        padding: 20px 0px 20px 0px;
    }
    .about-descripcion{
        width:  100%;
        padding: 0 10px 0 10px;
    }
    .about-descripcion>h3{
        font-size: 30px;
    }
    
    .about-img{
        width:  100%;
        padding: 20px 0 20px 0;
    }
 }
 /* Tablet*/
 @media (min-width: 768px) and (max-width: 1024px) {
    .about-container{
        padding: 30px 0 30px 0;
    }
    .about-descripcion{
        width:  100%;
        padding: 0 20px 0 20px;
        
    }
    .about-descripcion>h3{
        font-size: 30px;
    }
    .about-img{
        width:  100%;
        padding: 20px 0 20px 0;
    }
    .about-img-img{
        max-width: 400px;
        width: auto;
        height: auto;
        margin: 20px;
    }
    
    
  }
 